        methods:{
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            handleSelect(item) {
                console.log(item);
            },
            //获取企业列表
            getCompanyList(){
                var that = this;
                that.$axios.get('/industrialdeclaration/getCompanyList')
                    .then((res)=>{
                        if (res.data.code==0){
                            var arr = [];
                            res.data.data.forEach((item,key)=>{
                                arr.push({value:item.companyName})

                            })
                            that.restaurants = arr;
                        }
                    })
            },
            //时间转换
            //转换时间格式
            formateDate(datetime) {
                function addDateZero(num) {
                    return (num < 10 ? "0" + num : num);
                }
                let d = new Date(datetime);
                let formatdatetime = d.getFullYear() + '-' + addDateZero(d.getMonth() + 1) + '-' + addDateZero(d.getDate()) + ' ' + addDateZero(d.getHours()) + ':' + addDateZero(d.getMinutes()) + ':' + addDateZero(d.getSeconds());
                return formatdatetime;
            },
            //提交评价
            summitEvaluation(i,id){
                var that = this;
                var demandsId = id;
                var username  = 'admin';
                var commentContent = that.input; //评价内容
                that.$axios.post('/demandsComment/addDemandsComment',that.$qs.stringify({
                    demandsId:demandsId,
                    username:username,
                    commentContent:commentContent
                })).then((res)=>{
                    if (res.data.code==0){
                        that.$axios.get('/demandsComment/showCommentByPage',{
                            params:{
                                demandsID:id,
                                pageNum:1,
                                pagesize:10
                            }
                        }).then((res)=>{
                            if (res.data.code==0){
                                res.data.data.list.forEach((item,index)=>{
                                    res.data.data.list[index].createTime = that.formateDate(item.createTime);
                                    if (res.data.data.list[index].status == 0){
                                        res.data.data.list[index].statusText = '未审核';
                                    }
                                    if (res.data.data.list[index].status == 1){
                                        res.data.data.list[index].statusText = '已审核';
                                    }
                                    if (res.data.data.list[index].status == 2){
                                        res.data.data.list[index].statusText = '已驳回';
                                    }
                                })
                                that.leaveMessage[i] = res.data.data.list;
                                this.$forceUpdate();
                            }
                        })
                    }
                })

            },
            handleSizeChange(val) {
            },
            handleCurrentChange(val) {
                var num = val;

                if(this.flag==true){
                    this.search(val)
                }else {
                    this.getTecInfo(num);
                }
                this.Message = {
                    1:false,
                    2:false,
                    3:false,
                    4:false,
                    5:false,
                    6:false,
                    7:false,
                    8:false,
                    9:false,
                    10:false
                };
            },
            //检索
            search(val){
                var that = this;
                var declarationType = this.value;
                var companyName = this.cooperationType;
                if (declarationType == '全部'){
                    declarationType='';
                }
                var declarationYear = this.demandType;
                if (declarationYear== '全部'){
                    declarationYear = '';
                }else {
                    declarationYear = declarationYear.substring(0,4);
                }
                var pageSize = 10;
                var pageNum = val;
                console.log(pageNum)
                that.$axios.get('/industrialdeclaration/showByPage',{
                    params:{
                        declarationYear:declarationYear,
                        companyName:companyName,
                        declarationType:declarationType,
                        pageSize:pageSize,
                        pageNum:pageNum
                    }
                }).then((res)=> {
                    if (res.data.code==0){
                        if (!that.flag){
                            this.$message({
                                message: '检索成功!',
                                type: 'success'
                            });
                        }
                        that.totalPages = res.data.data.total;
                        if(res.data.data.total>10){
                            that.flag = true;
                        }
                        res.data.data.list.forEach((item,index)=>{
                            res.data.data.list[index].endDateT = that.formateDate(item.endDate);
                            if (res.data.data.list[index].status == 0){
                                res.data.data.list[index].statusText = '未审核';
                            }
                            if (res.data.data.list[index].status == 1){
                                res.data.data.list[index].statusText = '已审核';
                            }
                            if (res.data.data.list[index].status == 2){
                                res.data.data.list[index].statusText = '已驳回';
                            }
                        })
                        that.TecInfo = res.data.data.list;
                    }
                })
            },
            //跳转到当前页面
            toPages(){
            },
            //获取页码数
            getSize(a){
            },
            //获取科技需求信息
            getTecInfo(val){
                var that = this;
                var val = val||1;
                that.$axios.get('/industrialdeclaration/showByPage',{
                    params:{
                        pageSize:'10',
                        pageNum:val
                    }
                }).then((res)=>{
                    if (res.data.code==0){
                        that.totalPages = res.data.data.total;
                        res.data.data.list.forEach((item,index)=>{
                            res.data.data.list[index].createTime = that.formateDate(item.createTime);
                            if (res.data.data.list[index].status == 0){
                                res.data.data.list[index].statusText = '未审核';
                            }
                            if (res.data.data.list[index].status == 1){
                                res.data.data.list[index].statusText = '已审核';
                            }
